.v-data-table {
  background-color: unset!important;
}

pre {
  font-family: inherit!important;
}

.v-data-table--dense >>> th {
  font-size: 13px;
}

.v-data-table--dense >>> td {
  height: 24px;
  width: 50%;
}

.detailTable{
  background-color: transparent;
}

.v-item--active {
  color: #222;
  background-color: #ffe500 !important;
}
